*, *:before, *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

body {
  background: floralwhite; }

.stage-holder .stage {
  height: 640px;
  width: 360px;
  text-align: center;
  margin: 0 auto;
  background-color: #1a237e !important;
  position: relative; }

.stage-holder .stage .options-bar .option {
  display: inline-block;
  margin: 10px 25px;
  cursor: pointer; }

.stage-holder .stage .options-bar .name {
  color: white;
  font-size: 10px; }

.stage-holder .stage .add-btn {
  position: absolute;
  bottom: 10px;
  left: 160px; }

.stage-holder .stage .timepicker {
  display: none !important; }

.alarm-card {
  color: white;
  text-align: left; }

.alarm-card .row {
  border-bottom: thin bold white; }

.alarm-card .row .time, .alarm-card .row .toggler, .alarm-card .row .period, .alarm-card .row .arrow {
  display: inline-block; }

.alarm-card .row .time {
  font-size: 60px;
  margin: 10px 25px;
  cursor: pointer; }

.alarm-card .row .time span {
  font-size: 20px;
  margin: 0 10px;
  cursor: pointer; }

.alarm-card .row .toggler {
  float: right;
  margin: 35px 25px 0; }

.alarm-card .row .period {
  font-weight: bold;
  margin: 10px 25px;
  cursor: pointer; }

.alarm-card .row .arrow {
  float: right;
  margin-right: 25px;
  cursor: pointer;
  position: relative;
  top: 5px; }

svg.ap-icon {
  color: white !important;
  width: 20px; }



